html, body {
    height: 100%;
    width: 100%;
}

#Ticket {
    display: grid;
    width: 280px;
    text-align: center;
    font-size: 34px;
}

#Footer {
    font-size: 14px;
}

#Message {
    font-size: 14px;
    margin-bottom: 30px;
}

#LogoContainer {
    margin: 30px;
}

#TopContent {
    margin-bottom: 30px;
}

#Logo {
    max-width:230px;
    max-height:55px;
    width: auto;
    height: auto;
}

#FooterLogo {
    max-width:230px;
    max-height:55px;
    width: auto;
    height: auto;
}

#FooterLogoContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    min-height: 50px;
    width: 100%;
}

#FooterDateAndTimeContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}



